import CartContext from "./cart-context";
import { useReducer } from "react";
const defaultState = {
  items: [],
  totalAmount: 0,
};
const cartReducer = (state, action) => {
  if (action.type === "Add") {
    const ubdatedAmount =
      state.totalAmount + action.item.price * action.item.amount;
    const exsistingCartItem = state.items.findIndex(
      (item) => item.id === action.item.id
    );
    const exsistingItem = state.items[exsistingCartItem];
    let ubdatedItems;
    if (exsistingItem) {
      const ubdatedItem = {
        ...exsistingItem,
        amount: exsistingItem.amount + action.item.amount,
      };
      ubdatedItems = [...state.items];
      ubdatedItems[exsistingCartItem] = ubdatedItem;
    } else {
      ubdatedItems = state.items.concat(action.item);
    }
    return { items: ubdatedItems, totalAmount: ubdatedAmount };
  }
  if (action.type === "Remove") {
    const exsistingCartItem = state.items.findIndex(
      (item) => item.id === action.id
    );
    const exsistingItem = state.items[exsistingCartItem];
    const ubdatedAmount = state.totalAmount - exsistingItem.price;
    let ubdateItmes;
    if (exsistingItem.amount === 1) {
      ubdateItmes = state.items.filter((item) => item.id !== action.id);
    } else {
      const ubdatedItem = {
        ...exsistingItem,
        amount: exsistingItem.amount - 1,
      };
      ubdateItmes = [...state.items];
      ubdateItmes[exsistingCartItem] = ubdatedItem;
    }
    return { items: ubdateItmes, totalAmount: ubdatedAmount };
  }
  return defaultState;
};

const CartProvider = (props) => {
  const [CartState, dispatchCart] = useReducer(cartReducer, defaultState);

  const addItemsHandler = (item) => {
    dispatchCart({
      type: "Add",
      item: item,
    });
  };
  const removeItemsHandler = (id) => {
    dispatchCart({ type: "Remove", id: id });
  };
  const cartval = {
    items: CartState.items,
    totalAmount: CartState.totalAmount,
    addItems: addItemsHandler,
    removeItems: removeItemsHandler,
  };
  return (
    <CartContext.Provider value={cartval}>
      {props.children}
    </CartContext.Provider>
  );
};
export default CartProvider;
